<template>
  <div class="container" style="margin: 15px 15px;">
    <!-- 页面名字 -->
    <el-row type="flex" justify="start" style=" font-size: 16px; color: #303133;">楼盘管理</el-row>
    <el-row type="flex" justify="end" style="margin-top: 10px;">
      <el-button plain size="small">
        <router-link :to="{ path: '/property/create', query: { pageState: 1 }}"> 添加楼盘</router-link>
      </el-button>
    </el-row>
    <!-- 资讯列表 -->
    <el-row style="margin-top: 30px;">
      <el-table :data="buildings" :stripe="true" style="width: 100%" highlight-current-row>
        <el-table-column label="封面图" min-width="150">
          <template slot-scope="scope">
            <img :src="'https://szebra.cn' + scope.row.cover.url" style="width: 40px; height: 40px;background-color: #eee;">
          </template>
        </el-table-column>
        <el-table-column prop="name" label="名称" min-width="150">
        </el-table-column>
        <el-table-column prop="address" label="地址" min-width="150" show-overflow-tooltip>
        </el-table-column>
        <el-table-column prop="price_per_sqm" label="价格" min-width="150" show-overflow-tooltip>
        </el-table-column>
        <!-- need fix -->
        <!-- api 返回，缺少关注人数字段 -->
        <el-table-column prop ="" label="关注人数" min-width="150" show-overflow-tooltip>
        </el-table-column>
        <el-table-column label="操作" min-width="300">
          <template slot-scope="scope">
            <el-button type="text" size="small">
              <router-link class='el-button--text' :to="{ path: `/property/${scope.row.id}/detail` }">编辑</router-link>
            </el-button>
            <el-button  @click="deleteHandleClick(scope.row, 'delete')" type="text" size="small">删除</el-button>
            <el-button  @click="deleteHandleClick(scope.row, 'totalFollow')" type="text" size="small">清空总关注人数</el-button>
            <el-button  @click="deleteHandleClick(scope.row, 'myFollow')" type="text" size="small">清空我的关注人数</el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-row>
    <el-row style="margin-top:30px;"  type="flex" justify="end">
      <el-pagination @size-change="handleSizeChange"
                     @current-change="handleCurrentChange"
                     :current-page="curPage"
                     :page-sizes="[10, 20, 30, 40]"
                     :page-size="pageSize"
                     layout="total, sizes, prev, pager, next, jumper"
                     :total="total" />
    </el-row>
  </div>
</template>
<script>

export default {
  data () {
    return {
      currentRow: null,
      dialogType: '',
      dialogMsg: '',
      dialogVisible: false,
      buildings: [],
      curPage: 1,
      pageSize: 10,
      total: 0
    }
  },

  components: {
  },

  methods: {
    // 获取信息列表
    queryList () {
      let params = {
        page: this.curPage,
        per: this.pageSize
      }
      this.$http.buildingList(params).then((res) => {
        if (!res) return
        let result = res.data.result_rows
        this.buildings = result.buildings || []
        this.total = result.page.total_count
      })
    },
    // page大小改变
    handleSizeChange (page) {
      this.pageSize = page
      this.queryList()
    },

    // 当前page改变
    handleCurrentChange (page) {
      this.curPage = page
      this.queryList()
    },
    // 删除操作
    deleteHandleClick (row, type) {
      let dialogMsg, deleteFunc
      switch (type) {
        case 'delete':
          dialogMsg = '删除后将清空该楼盘的信息，确定删除吗？'
          deleteFunc = 'buildingDelete'
          break
        case 'totalFollow':
          dialogMsg = '确定清零总关注数吗？'
          deleteFunc = 'clearTotalFollow'
          break
        case 'myFollow':
          dialogMsg = '确定清零我的注数吗？'
          deleteFunc = 'clearMyFollow'
          break
      }
      this.$confirm(dialogMsg, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
      .then((r) => {
        this.$http[deleteFunc](row, 'delete').then(res => {
          if (!res) return
          this.$message.success('操作成功')
          this.queryList()
        })
      })
      .catch(() => {})
    }
  },
  created () {
    this.queryList()
  }
}
</script>

<style>
</style>
